header {
  color: #fff;
  background-color: red;
  padding: .5rem 1rem;
  font-weight: 500;
}

#app {
  height: 100vh;
}

.gcode-sender {
  grid-template-areas: "toolbar toolbar"
                       "gcode preview"
                       "controls preview"
                       "jogpad preview"
                       "logs logs";
  gap: .5rem;
  height: 100%;
  padding: .5rem;
  display: grid;
}

.gcode-sender .__toolbar {
  flex-flow: wrap;
  grid-area: toolbar;
  align-items: center;
  gap: .5rem;
  display: flex;
}

.gcode-sender .__logs {
  background: #d6d6d6;
  flex-direction: column-reverse;
  grid-area: logs;
  min-height: 16rem;
  max-height: 50vh;
  margin: 0 -.5rem -.5rem;
  padding: .5rem;
  display: flex;
  overflow: auto;
}

.gcode-sender .__gcode {
  grid-area: gcode;
  min-width: 30vw;
  min-height: 10rem;
}

.gcode-sender .__jog-pad {
  grid-area: jogpad;
}

.gcode-sender .__gcode-preview {
  grid-area: preview;
  min-width: 50vw;
  min-height: 50vh;
}

@media only screen and (width <= 767px) {
  .gcode-sender {
    grid-template-areas: "toolbar"
                         "gcode"
                         "preview"
                         "controls"
                         "jogpad"
                         "logs";
  }
}

.jog-pad {
  grid-template-columns: repeat(3, 1fr);
  gap: .25rem;
  width: min-content;
  height: min-content;
  display: grid;
}

.jog-pad > button {
  width: 3rem;
  height: 3rem;
}

.gcode-viewer {
  background: #0b71b3;
}

.gcode-viewer svg {
  width: 100%;
  height: 100%;
}

.gcode-viewer svg line {
  vector-effect: non-scaling-stroke;
  stroke: red;
}

.gcode-viewer svg line.gcode-g0 {
  stroke: #81b590aa;
}

.gcode-viewer svg g.__viewport {
  transform: translate(0)scale(3)translate(5px, 5px);
}

main {
  grid-template-rows: auto 1fr;
  height: 100%;
  display: grid;
}

