.gcode-viewer {
    background: #0B71B3;

    svg {
        width: 100%;
        height: 100%;

        line {
            vector-effect: non-scaling-stroke;
            stroke: #ff0000;

            &.gcode-g0 {
                stroke: #81b590AA;
            }
        }

        g.__viewport {
            transform: translate(0px, 0%) scale(3, 3) translate(5px, 5px);
        }
    }
}